import Vue from "vue";
import App from "./App.vue";
import store from "./store";
import vuetify from "./plugins/vuetify";
import "./scss/style.scss";
import Meta from "vue-meta";
Vue.use(Meta);

Vue.config.productionTip = true;

new Vue({
  store,
  vuetify,
  render: function(h) {
    return h(App);
  },
}).$mount("#app");
